.home-div{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color:$primary;

      h4{
          margin-top: 32px;
          font-family: $fontStyle1;
          font-weight: 1000;
          color: $secondary1;
          
      }

      span{
          color: $lightGrey;
      }

      .top-product-div{
          width: 70%;
          display: flex;
          padding: 8px;
      }

      .features-div{
          width: 100%;
          display: flex;
          height: 300px;
          margin: 32px;
          padding:16px;
          display: flex;
          align-items: center;
          justify-content: center;
          background-color: white;
      }


}


  